/**
 * Drawing mode
 */
.amcharts-export-canvas {
	position: absolute;
	display: none;
	z-index: 1;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #fff;
}
.amcharts-export-canvas.active {
	display: block;
}

