	position: absolute;
	z-index: 2;
	opacity: 0.5;
	color: #000;
}
.amcharts-main-div:hover .amcharts-export-menu, .amcharts-stock-div:hover .amcharts-export-menu, .amcharts-export-menu.active {
	opacity: 1;
}

