.amcharts-export-menu ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
.amcharts-export-menu li {
	position: relative;
	display: block;
	z-index: 1;
}
.amcharts-export-menu li > ul {
	position: absolute;
	display: none;
	border: 1px solid #e2e2e2;
	margin-top: -1px;
	background: #fff;
}
.amcharts-export-menu li > a {
	position: relative;
	display: block;
	color: #000;
	text-decoration: none;
	padding: 12px 12px;
	z-index: 2;
	white-space: nowrap;
	border-bottom: 1px solid #f2f2f2;
}
.amcharts-export-menu li:last-child > a {
	border-bottom: none;
}
.amcharts-export-menu li > a > img {
	border: none;
}
.amcharts-export-menu-top-left {
	top: 0;
	left: 0;
}
.amcharts-export-menu-bottom-left {
	bottom: 0;
	left: 0;
}
.amcharts-export-menu-top-right {
	top: 0;
	right: 0;
}
.amcharts-export-menu-bottom-right {
	bottom: 0;
	right: 0;
}

/**
 * Menu; Hover state
 */
.amcharts-export-menu li:hover > ul,
.amcharts-export-menu li.active > ul {
	display: block;
}
.amcharts-export-menu li:hover > a,
.amcharts-export-menu li.active > a {
	color: #fff;
	background-color: #636363;
}
.amcharts-export-menu-top-left li:hover > ul,
.amcharts-export-menu-top-left li.active > ul {
	left: 100%;
	top: 0;
}
.amcharts-export-menu-bottom-left li:hover > ul,
.amcharts-export-menu-bottom-left li.active > ul {
	left: 100%;
	bottom: 0;
}
.amcharts-export-menu-top-right li:hover > ul,
.amcharts-export-menu-top-right li.active > ul {
	top: 0;
	right: 100%;
}
.amcharts-export-menu-bottom-right li:hover > ul,
.amcharts-export-menu-bottom-right li.active > ul {
	bottom: 0;
	right: 100%;
}

