/**
 * Menu; Color picker
 */

.amcharts-export-menu .export-drawing-color {
	background: #000;
	width: 35px;
}
.amcharts-export-menu .export-drawing-color > a {
	display: block;
	overflow: hidden;
	text-indent: -13333337px;
}
.amcharts-export-menu .export-drawing-color-red {
	background: #f00;
}
.amcharts-export-menu .export-drawing-color-green {
	background: #0f0;
}
.amcharts-export-menu .export-drawing-color-blue {
	background: #00f;
}
.amcharts-export-menu .export-drawing-color-white {
	background: #fff;
}

/*
** Fallback
*/
.amcharts-export-fallback {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #fff;
}
.amcharts-export-fallback textarea {
	border: none;
	outline: none;
	position: absolute;
	overflow: hidden;
	width: 100%;
	height: 100%;
	padding: 20px;
}
.amcharts-export-fallback-message {
	position: absolute;
	z-index: 1;
	padding: 20px;
	width: 100%;
	background-color: #fff;
}

/*
** DELAYED CAPTURING
*/
.amcharts-export-menu .export-delayed-capturing > a {
	text-indent: 0px;
	line-height: 36px;
	vertical-align: middle;
	text-align: center;
	background-image: none;
}

/*
** TRANSITION; OPACITY
*/
.amcharts-export-menu,
.amcharts-export-canvas .canvas-container {
	-webkit-transition: opacity 0.5s ease-out;
	-moz-transition: opacity 0.5s ease-out;
	-ms-transition: opacity 0.5s ease-out;
	-o-transition: opacity 0.5s ease-out;
	transition: opacity 0.5s ease-out;
}
.amcharts-export-canvas.dropbox .canvas-container {
	opacity: 0.5;
}


/*
** SHAPE
*/
.amcharts-export-menu .export-drawing-shape a {
	font: 0/0 a;
	text-shadow: none;
	color: transparent;
}
.amcharts-export-menu li img {
	height: 20px;
}


/*
** BRUSH
*/
.amcharts-export-menu .export-drawing-width a {
	text-align: center;
}
.amcharts-export-menu .export-drawing-width span {
	display: block;
	margin: 0 auto;
}
.amcharts-export-menu .export-drawing-width span > span {
	display: block;
	background: #000;
	border-radius: 100%;
}
.amcharts-export-menu .export-drawing-shape a:hover img,
.amcharts-export-menu .export-drawing-shape.active a img {
	-webkit-filter: invert(100%);
	filter: invert(100%);
}